<h1 id="python-lists" tabindex="-1"><a class="header-anchor" href="#python-lists" aria-hidden="true">#</a> Python Lists</h1>
<p>Sometimes you only need to remember a single value, such as a year the user has entered, but most often you rather need to remember many values of the same type, such as:</p>
<ul>
<li>All the years the user has entered to your program.</li>
<li>The name of all your friends.</li>
<li>The name of all the files in a directory.</li>
</ul>
<p>Instead of having 50 variables to store 50 different values (which also limits out program to handle at most 50 different values), we can put as many values as we want (and need) in a list. Let's see how to do that in this lecture.</p>
